import React from 'react'
import Main from './main'
import Aside from './aside'
import { wyrm_list_5 } from '../data/wyrmprints-5.js'
import { special_regex } from '../data/convert'
import './body.css'

const wyrm_list = wyrm_list_5

class Body extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      lists: wyrm_list
    }
    this.reset = this.reset.bind(this)
  }
  render() {
    return (
      <div className='body'>
        <Main value={this.state.lists}/>
        <Aside handleSubmit={data => this.searchAll(data)} handleReset={this.reset}/>
      </div>
    );
  }
  searchAll(data) {
    let temp = []
    temp = this.searchName(data.name, wyrm_list)
    temp = this.searchEffects(data, temp)
    this.setState({lists: temp})
  }
  searchName(name, array) {
    if (name === '') {
      return array
    }
    let temp = []
    let regex = new RegExp('.*' + name + '.*')
    for (let i = 0; i < array.length; i++) {
      if (array[i].name.match(regex)) {
        temp.push(array[i])
      }
    }
    return temp
  }
  searchEffects(data, array) {
    if (data.exclusive === '' && data.condition === '' && data.effect === '') {
      return array 
    }
    let str = ''
    if (data.exclusive !== '') str += '„Äê' + data.exclusive + '„Äë'
    if (data.condition !== '') str += data.condition
    if (data.effect !== '') {
      if (special_regex[data.effect]) {
        str += '(' + data.effect + '|' + special_regex[data.effect] + ')'
      } else {
        str += data.effect
      }
    }
    let regex = new RegExp(str)
    console.log(regex)
    let temp = []
    for (let i = 0; i < array.length; i++) {
      for  (let j = 0; j < array[i].effects.length; j++) {
        if (array[i].effects[j].match(regex)) {
          temp.push(array[i])
          break
        }
      }
    }
    return temp
  }
  reset() {
    this.setState({lists: wyrm_list})
  }
}

export default Body